import React, { Component } from 'react';
import text from './Data/data';
import Paragraphs from './Paragraphs';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 0,
      arr: []
    };
  }
  handleChange = (e)=>{
    let val = e.target.value;
    if(val<=0)
    val = 0;
    this.setState({
      count: val
    })
  }
  handleSubmit = (e)=>{
    e.preventDefault();
    let val = this.state.count;
    if(val>15)
    val = 15;
    if(val===0)
    val=1;
    let data = text.slice(0,val);
    console.log(data);
    this.setState({
      arr: data
    })
  }
  render() {
    return (
      <div className="container">
        <h3>Lorem Ipsum Project</h3>
        <form className="form-group">
          <input type="number"  onChange={this.handleChange} value={this.state.count} className="btn"/>
          <button type="submit" onClick={this.handleSubmit} className="btn btn-primary">Generate</button>
        </form>
        <Paragraphs data={this.state.arr}/>
      </div>
    );
  }
}

export default App;

